import { React, useState, useEffect } from 'react'
import Header from '../components/Header'
import PriceTableRow from '../components/PriceTableRow'
import { useParams } from 'react-router'
import { BACKEND_URL } from '../App'
let itemData;

function ItemPage() {
    const [isDataLoaded, setIsDataLoaded] = useState(false);

    const { productName } = useParams();


    


    const getData = async () => {
        console.log("Product Name is " + productName)
        itemData = await fetch(`${BACKEND_URL}/product?name=${productName}`, {
            headers: {
                'Content-Type': 'application/json'

            },
        })



        

        itemData = await itemData.json();
        console.log("Item Data is " + JSON.stringify(itemData))



        setIsDataLoaded(true);


    }


    useEffect(async () => {

        await getData();


    }, []);

    if (isDataLoaded) {
        
            return (
                <div className="ItemPage">
                    <Header />
                    <div className="ItemPage-inner">
                        <div className="imgSide">
                            <div className="img">
                            <img src={itemData.imgUrl}/>
                            </div>
                                
                            
                        </div>
                        <div className="ItemPage-price-side">
                            <h2>{itemData.ProductName}</h2>
                            <h4>Current Prices</h4>
                            <table className="PriceTable">
                                <thead>
                                    <tr>
                                        <th>Retailer</th>
                                        <th>Price</th>
                                        {/* <th>Change</th> */}
                                    </tr>
                                </thead>
                                <tbody>
                                    <PriceTableRow logo="https://upload.wikimedia.org/wikipedia/commons/c/c4/Best_Buy_logo_2018.svg"
                                        price={itemData.Prices.BestBuy} href={itemData.Links.Bestbuy} />
                                    <PriceTableRow logo="https://upload.wikimedia.org/wikipedia/commons/3/3e/Micro_Center_logo.svg"
                                        price={itemData.Prices.MicroCenter} href={itemData.Links.MicroCenter} />
                                    <PriceTableRow logo="https://upload.wikimedia.org/wikipedia/commons/a/a9/Amazon_logo.svg"
                                        price={itemData.Prices.Amazon} href={itemData.Links.Amazon} />
                                    <PriceTableRow logo="https://cdn.freelogovectors.net/wp-content/uploads/2018/11/newegg-logo.png"
                                        price={itemData.Prices.Newegg} href={itemData.Links.Newegg} />
                                </tbody>

                            </table>
                        </div>
                    </div>
                </div>



            )
        
    }
    else {
        return (<h1>Loading</h1>)
    }


}

export default ItemPage
