import React from 'react'

function Filter(props) {
if(props.term){
    return (
        <div className="filter">
            <div>
            <text className="SearchResult">Showing Results for "{props.term}"</text>
            </div>
           
            
            <hr></hr>
        </div>
    )
}
else{
    return (
        <div className="filter">
            <div>
            <text className="filter-menu"></text>
            </div>
           
            
            <hr></hr>
        </div>
    )
}
   
}

export default Filter
